<style scoped lang="scss">
@import "@/assets/css/public/var.scss";

.sort {
  position: relative;
  width: 230px;
  height: 425px;
  display: inline-block;
  position: relative;
  vertical-align: text-bottom;
  z-index: 20;
  dl {
    margin-top: -50px;
    height: 475px;
    background: #fff;
    box-sizing: border-box;
    border: 1px solid #e5e5e5;
    width: 100%;
    a {
      color: #646464;
      font-size: 13px;
    }
    dt {
      color: #222;
      font-weight: 700;
      height: 50px;
      padding-top: 15px;
      span {
        margin-left: 15px;
      }
    }
    dd {
      font-size: 13px;
      padding: 2px 12px;
      .hot {
        display: inline-block;
        margin-left: 5px;
        background: #fff3cc;
        color: #222;
        padding: 0 7px;
        border-radius: 10px;
      }
      a {
        color: #646464;
      }
      &:hover {
        background: rgba(255, 150, 0, 0.08);
        a {
          color: #222;
          font-weight: 700;
        }
        .hot {
          background: linear-gradient(to bottom right, #ffd000, #ffbd00);
        }
        .arrow {
          border-color: #222;
        }
      }
      i{
          margin-right: 11px;
      }
      i::before {
        width: 16px;
        height: 16px;
        vertical-align: middle;
      }
    }
    .arrow {
      float: right;
      width: 4px;
      height: 4px;
      margin-top: 8px;
      border: {
        top: 1px solid #bfbfbf;
        right: 1px solid #bfbfbf;
      }
      transform: rotate(45deg);
    }
  }
  .detail {
    position: absolute;
    width: 400px;
    height: 416px;
    left: 230px;
    top: 5px;
    background: #fff;
    dl {
      width: 100%;
      box-sizing: border-box;
      padding: 0 30px;
      height: auto;
      margin-top: 0;
      border: none;
      dt {
        height: 22px;
        padding-bottom: 10px;
        font-weight: normal;
        padding-top: 0;
        border-bottom: 1px solid #e5e5e5;
        margin-top: 24px;
        .more {
          font-weight: normal;
          float: right;
          a {
            color: #999;
            font-size: 12px;
            margin-right: 5px;
          }
        }
        .arrow {
          margin-top: 10px;
        }
      }
      dd {
        float: left;
        margin: 10px 16px 0 0;
        padding: 0;
        a {
          color: #999;
          &:hover {
            color: $hoverColor;
          }
        }
        &:hover {
          background: none;
          a {
            font-weight: normal;
          }
        }
      }
    }
  }
}
</style>


